.checkbox-label {
  display: flex;
  align-items: center;

  &__label {
    max-width: calc(100% - 32px);
    margin-left: 8px;
    font-weight: 700;
    font-size: 16px;
  }

  &.orange {
    align-items: flex-start;

    .checkbox {
      margin-top: 2px;

      &:checked {
        background-image: url($imgUrl + 'checkbox-checked-orange.png');
      }
    }

    .checkbox-label__label {
      color: #ec6602;
      font-size: 18px;
      line-height: 1.44;
      word-break: keep-all;
    }
  }
}

.checkbox {
  appearance: none;
  width: 24px;
  height: 24px;
  background-repeat: no-repeat;
  background-size: 100%;
  background-image: url($imgUrl + 'checkbox-default.png');

  &:checked {
    background-image: url($imgUrl + 'checkbox-checked.png');
  }
}