.sch-box {
  display: flex;
  gap: 0 5px;

  &__select {
    height: 35px;
    padding: 0 10px;
    font-size: 16px;
    border: 1px solid #ddd;
    border-radius: 5px;
  }

  &__input {
    height: 35px;
    font-size: 16px;
    border: 1px solid #ddd;
    border-radius: 5px;
  }

  &__submit {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 35px;
    min-width: 80px;
    color: #fff;
    font-size: 16px;
    background: #578650;
    border-radius: 5px;

    &:hover {
      opacity: 0.8;
    }
  }
}

.paging {
  display: flex;
  justify-content: center;
  gap: 0 8px;

  a {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 24px;
    border: 1px solid #ddd;
    border-radius: 5px;

    &:hover,
    &.active {
      color: #6ec461;
      border-color: #6ec461;
    }
  }

  &__num {
    font-size: 14px;
  }
}

.form-el {
  position: relative;
  padding: 12px 24px;

  &__label {
    position: relative;
    color: #666;
    font-size: 14px;

    & + .input-txt {
      margin-top: 4px;
    }

    &--rq:after {
      content: '*';
      position: absolute;
      top: 1px;
      right: -8px;
      color: #ec6602;
      font-size: 18px;
      line-height: 1;
    }
  }

  .input-txt {

    &__input {
      padding-bottom: 9px;
    }
  }

  .error-txt {
    position: absolute;
    top: 14px;
    right: 20px;
  }
}

.inp-list {
  border: 1px solid #eee;
  border-radius: 8px;

  &__item {
    border-bottom: 1px solid #eee;

    &.disabled {
      background: #ededed;
      border-bottom: 1px solid #e6e6e6;
    }

    &:last-child {
      border-bottom: 0;
    }

    &.item-sn {
      display: none;

      &.show {
        display: block;
      }
    }
  }
}

.input-txt {

  &__input {
    width: 100%;
    height: 26px;
    font-size: 18px;
    border: 0 solid #000;
    outline: none;

    &::placeholder {
      color: #999;
      font-size: 18px;
    }
  }
}

.radiocircle {
  appearance: none;
}

.bs-combo {

  &__btn {
    display: flex;
    align-items: center;
    width: 100%;
    height: 26px;
    background: url($imgUrl + 'dropdown-down.png') 100% 50%/15px no-repeat;
  }

  &__btn-txt {
    font-size: 18px;
  }

  &__box {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: -90;
    opacity: 0;
    background: rgba(0, 0, 0, 0.7);
    transition: 0.2s;

    &.open {
      z-index: 90;
      opacity: 1;

      .bs-combo__cont {
        bottom: 0;
      }
    }
  }

  &__cont {
    position: absolute;
    bottom: -100%;
    left: 0;
    right: 0;
    padding: 20px 25px;
    background: #fff;
    border-radius: 20px 20px 0 0;
    transition: 0.3s;
  }

  &__tit {
    padding-left: 30px;
    color: #bebebe;
    font-weight: 300;
    font-size: 24px;
  }

  &__list {
    margin-top: 10px;
  }

  &__item {
    padding: 10px 30px;
    border-bottom: 1px solid #eee;

    &:last-child {
      border-bottom: 0;
    }
  }

  &__txt {
    font-weight: 300;
    font-size: 24px;
    max-width: calc(100% - 30px);
  }

  &__label {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.textarea {

  &__txt {
    width: 100%;
    height: 80px;
    margin-top: 3px;
    font-size: 16px;
    line-height: 22px;
    border: 0;
    outline: none;
  }

  &__cnt {
    display: flex;
    justify-content: flex-end;
    color: #999;
    font-size: 13px;
  }
}

.radiocircle {
  width: 20px;
  height: 20px;
  background-repeat: no-repeat;
  background-size: 20px 20px;
  background-image: url($imgUrl + 'radio-default.png');

  &:checked {
    background-image: url($imgUrl + 'radio-checked.png');
  }
}

.multi-file-upload {

  .file-item {
    margin-bottom: 5px;
  }
}

.mfu {

  &__item {
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;

    &:last-child {
      margin-bottom: 0;
    }

    .mfu__file-txt {
      width: calc(100% - 68px);
      padding: 0 17px;
      color: #666;
      font-size: 12px;
      border-radius: 8px;
      border: solid 1px #eee;
    }

    .btn.remove {
      width: 64px;
      margin-left: 4px;
      font-size: 14px;
    }
  }

  &__info {
    display: flex;

    .add-row {
      width: 86px;
      margin-right: 16px;
    }

    .mfu__guide {
      width: calc(100% - 102px);

      .g-txt {
        color: #707070;
        font-weight: 300;
        font-size: 14px;
        line-height: 20px;

        span {
          color: #cf4b00;
        }
      }
    }
  }

  &__error {
    color: #ec6602;
    font-size: 16px;
    line-height: 26px;
  }
}

.agree-ta {

  &__box {
    overflow-y: auto;
    height: 146px;
    padding: 10px;
    background: #f6f6f6;
  }

  &__tit {
    display: block;
    color: #888;
    font-size: 12px;
  }

  &__txt {
    margin-top: 10px;
    color: #888;
    font-size: 12px;

    & + .agree-ta__tit {
      margin-top: 10px;
    }
  }

  .underline {
    color: #888;
    font-size: 12px;
    text-decoration: underline;
  }
}

.privacy-inp {

  &__item {
    margin-top: 20px;

    &:first-child {
      margin-top: 0;
    }

    &.is-hide {
      display: none;
    }
  }

  &__row {
    position: relative;
  }

  &__label {
    position: absolute;
    top: 50%;
    left: 14px;
    right: 70px;
    z-index: 2;
    font-size: 16px;
    transform: translateY(-50%);
    background: #fff;
    pointer-events: none;

    &.is-hide {
      display: none;
    }

    &:after {
      content: '*';
      color: #ec6602;
    }
  }

  &__input {
    width: 100%;
    height: 52px;
    padding-left: 15px;
    font-size: 16px;
    border: 1px solid #eee;
    border-radius: 4px;
  }

  &__select {
    position: relative;
    z-index: 1;
    width: 100%;
    height: 52px;
    padding-left: 15px;
    font-size: 16px;
    border: 1px solid #eee;
    border-radius: 4px;
    appearance: none;
    background: url($imgUrl + 'dropdown-down.png') calc(100% - 20px) 50%/15px no-repeat;
  }

  &__dr {
    position: absolute;
    top: 50%;
    z-index: 2;
    right: 15px;
    padding-left: 5px;
    color: #80A1D5;
    font-size: 13px;
    border-left: 1px solid #80A1D5;
    transform: translateY(-50%);
  }

  &__guide {
    margin-top: 6px;
    color: #666;
    font-size: 10px;
  }

  &__s-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__s-title {
    font-weight: 700;
    font-size: 18px;
  }

  &__s-clear {
    font-size: 14px;
  }

  &__s-pad {
    width: 100%;
    height: 146px;
    margin-top: 10px;
    border: 1px solid #dedede;
  }
}