.f-pop {
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 100;
  background: rgba(0, 0, 0, 0.7);

  &.open {
    display: block;
  }

  &__inner {
    height: 100%;
    padding-top: 60px;
  }

  &__container {
    position: relative;
    height: 100%;
    padding-top: 50px;
    background: #fff;
    border-radius: 20px 20px 0 0;
  }

  &__head {
    display: flex;
    align-items: center;
    height: 52px;
    padding: 0 25px;
    position: relative;
    font-weight: 700;
    font-size: 18px;
  }

  &__cont {
    height: calc(100% - 152px);
    margin-top: 20px;
    padding: 0 25px 20px;
    overflow-y: auto;
  }

  &__fixed-btn {
    height: 80px;
    padding: 12px;
  }

  &__btn-close {
    position: absolute;
    top: 16px;
    right: 24px;
    width: 24px;
    height: 24px;
    background: url($imgUrl + 'header-close.png') 0/24px;
  }

  &__btn-close-txt {
    display: block;
    text-indent: -9999em;
  }

  .agree-txt {
    color: #666;
    font-size: 14px;
    line-height: 24px;
    white-space: pre-line;

    strong {
      display: inline-block;
      font-size: 1em;
      margin-bottom: 5px;
    }

    .underline {
      text-decoration: underline;
    }
  }

  .privacy-box__table {
    margin-bottom: 15px;

    thead th {
      border-top: 1px solid #888;
      border-bottom: 1px solid #888;
      padding: 5px;
    }

    tbody td {
      padding: 5px;
    }  
  } 
}