.wrap {
  height: 100%;
}

.header {
  &__inner {
    position: relative;
    height: 60px;
  }

  &__logo {
    position: absolute;
    top: 19px;
    left: 24px;
    width: 92px;

    &.center {
      left: 50%;
      transform: translateX(-50%);
    }

    a {
      display: block;
      font-size: 0;
    }
  }

  &__close {
    position: absolute;
    top: 18px;
    right: 24px;
    width: 24px;
  }
}

.container {
  height: calc(100% - 60px);
  background: #fff;
}

.content {
  height: 100%;
  overflow-y: auto;

  &.wfb {
    height: calc(100% - 80px);
  }
}

.sub-head {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 35px;

  .page-tit {
    font-size: 24px;
    font-weight: 700;
  }
}

.sub-section-line {
  padding: 28px 24px;
  border-top: 4px solid #f8f8f8;

  .sub-head {
    margin-top: -10px;
  }
}

.issue-form {
  height: 100%;
}

.fixed-btn {
  padding: 12px;
}