}

body {
  height: 100%;
  margin: 0;
  font-family: "Noto Sans kr", -apple-system, BlinkMacSystemFont, "Helvetica Neue", "Apple SD Gothic Neo", Arial, sans-serif;
  color: #222;
  letter-spacing: -0.025em;
}
body.isScrollLock, body.isLayerShow, body.isLoading {
  overflow: hidden;
}

h1, h2, h3, h4, h5, h6, p, figure, ul, ol, dl, dd, fieldset, pre {
  margin: 0;
}

ol, ul, td, fieldset {
  padding: 0;
}

input, select, textarea, button, pre, a, code {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
  letter-spacing: inherit;
  color: inherit;
  text-decoration: none;
}

li {
