  border-collapse: collapse;
  border: 0;
}

fieldset, img {
  border: 0;
}

img {
  vertical-align: top;
  max-width: 100%;
}

label {
  cursor: pointer;
}

label, td img {
  vertical-align: middle;
}

h1, h2, h3, h4, h5, strong, b, th {
  font-weight: 700;
}

legend, caption {
  display: none;
}

address, em {
  font-style: normal;
}

button {
