
.process-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 56px;
  border-radius: 28px;

  &.w-full {
    width: 100%;
  }

  &.confirm {
    background: #ec6602;

    .process-btn__txt {
      color: #fff;
    }
  }

  &.confirm-gray {
    background: #999;

    .process-btn__txt {
      color: #fff;
    }
  }

  &.cancel {
    background: #dedede;
  }

  &[disabled] {
    background: #eee;

    .process-btn__txt {
      color: #666;
      font-weight: 700;
      font-size: 18px;
    }
  }

  &__txt {
    font-weight: 700;
    font-size: 18px;
  }
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 44px;
  border-radius: 8px;

  &.w-f {
    width: 100%;
  }

  &:hover {
    opacity: 0.7;
  }

  &.confirm {
    color: #fff;
    font-size: 16px;
    background: #099;
  }

  &.remove {
    color: #fff;
    font-size: 16px;
    background: #999;
  }
}

.btn-open-pop {
  display: flex;
  align-items: center;
  height: 56px;
  padding: 0 40px 0 12px;
  text-align: left;
  border: 1px solid #eee;
  border-radius: 8px;
  background: url($imgUrl + 'btn-pop-open-arrow.png') calc(100% - 10px) 50%/24px no-repeat;

  &__txt {
    font-size: 14px;
  }
}