.section-tit {

  .txt {
    font-weight: 700;
    font-size: 18px;
    line-height: 1.5;
  }

  &.required {

    .txt {

      &:after {
        content: '*';
        color: #ec6602;
        font-size: 18px;
      }
    }
  }
}

.reg-issue {

  .check-wrap {

    .check-tit {
      display: block;
      font-size: 14px;
      line-height: 1.5;
    }

    .check-txt {
      margin-top: 12px;
      color: #666;
      font-size: 14px;
    }
  }

  .section-head {

    &.clear {
      overflow: hidden;
    }

    .section-tit {
      float: left;
    }

    .error-txt {
      float: right;
      margin-top: 4px;
    }
  }
}

.complete-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;

  .cp-txt {
    display: block;
    font-weight: 700;
    font-size: 24px;
    line-height: 32px;
    text-align: center;
  }

  .cp-txt2 {
    position: relative;
    color: #666;
    margin-top: 94px;
    font-size: 14px;
    text-align: center;

    &:after {
      content: '';
      position: absolute;
      top: -69px;
      left: 50%;
      width: 44px;
      height: 44px;
      background-repeat: no-repeat;
      background-size: 100%;
      transform: translateX(-50%);
    }

    &.success {

      &:after {
        background-image: url($imgUrl + 'ico-confirm.png');
      }
    }

    &.fail {

      &:after {
        background-image: url($imgUrl + 'ico-failed.png');
      }
    }
  }
}

.privacy {
  
  .privacy-tit {
    font-size: 18px;
    text-align: center;
  }

  .privacy-sub-tit {
    margin-top: 20px;
    font-size: 12px;
    line-height: 20px;
  }
}

.agree-ta {

  .privacy-box__table {
    margin: 10px 0;
  }
  
  .privacy-box__table thead th {
    border-top: 1px solid #888;
    border-bottom: 1px solid #888;
    padding: 5px;
    font-size: 12px;
  }
  
  .privacy-box__table tbody td {
    padding: 5px;
    font-size: 12px;
  }
  
}